.elite {
    color: goldenrod;
    background: linear-gradient(to bottom right, grey, black);
}

.elite .btn-lg {
    background-color: goldenrod;
    border: none;
}

.elite .btn-lg:hover {
    background-color: rgb(165, 120, 5);
}

.elite .btn-lg:active {
    background-color: rgb(117, 90, 20);
}

.mzworkout {
    color: white;
    /*background: linear-gradient(to right, black 0%, rgb(61, 61, 61) 45%, rgb(161, 161, 161) 48%, rgb(221, 203, 203) 50%, rgb(255, 159, 159) 52%, rgb(255, 75, 75) 55%, red 100%);*/
    background: linear-gradient(to right, black 0%, rgb(31, 31, 31) 40%, rgb(187, 141, 141) 50%, rgb(255, 71, 71) 60%, red 100%);
}

.mzworkout>h2 {
    background-color: red;
    /* Sfondo rosso */
    color: white;
    /* Testo bianco per contrasto */
    padding: 1px 5px;
    /* Spazio interno per l'effetto evidenziato */
    display: inline;
    /* Mantiene lo sfondo rettangolare attorno al testo */
}

.adf {
    color: rgb(0, 0, 0);
    background: linear-gradient(to bottom right, rgb(212, 212, 212), rgb(24, 24, 24));
}

.square-img {
    aspect-ratio: 1 / 1;
    width: 100%;
    object-fit: cover;
    max-width: 150px;
}